import React , {useState, useEffect} from 'react'
import {Redirect, withRouter} from 'react-router-dom'

import './home.scss'
import Constellation from '../../Constellation/constellation';
import RadialNav from '../../Components/RadialNav/RadialNav';
import Credentials from './Credentials';
import Salutation from './Salutation';

function Home({history, location}){
    const [redirectPath, setRedirectPath] = useState(null)
    const [exit, setExit] = useState(false)
    var onRadialRedirect = (p) => {setExit(true); setRedirectPath(p)}
    var executeRedirectToPage = (event) => {
        if(event.target.id === "home" && redirectPath){ history.push(redirectPath)  }
    }
    var getHomeClassList = () => exit ? "home exit" : "home";
   
    useEffect( () => {
        return function cleanUp(){
            console.log('home is dead')
        }
    })
    return (<>
        <Constellation/>
        <div id="home" className={getHomeClassList()} onAnimationEnd={executeRedirectToPage}>
            <Salutation/>
            <RadialNav onRedirect={onRadialRedirect}/>
            <Credentials/>
        </div>
    </>)
}
export default withRouter(Home)