import React , {useState, useEffect} from 'react'
import {Redirect, withRouter} from 'react-router-dom'
import './radialnav.scss'
import CakeSvg from './CakeSvg';
import MenuBar from './MenuBar';

// const radialButtons = [ 
//     {direction : 'top', text : 'About', path : '/about', selected : false},
//     {direction : 'right', text : 'Skills', path : '/skills', selected : false},
//     {direction : 'bottom', text : 'Contact', path : '/contact', selected : false},
//     {direction : 'left', text : 'Gallery', path : '/gallery', selected : false}
// ]
var setN = (n) => { return {"--n" : n}}
function RadialNav({onRedirect}){
    const [isActive, setIsActive] = useState(false)
    const [buttons, setButtons] = useState(
        [ 
            {direction : 'top', text : 'About', path : '/about', selected : false},
            {direction : 'right', text : 'Skills', path : '/skills', selected : false},
            {direction : 'bottom', text : 'Contact', path : '/contact', selected : false},
            {direction : 'left', text : 'Gallery', path : '/gallery', selected : false}
        ]   
    )
    
    var onNavClick = () => { 
        setButtons(buttons.map(b => { b.selected = false; return b } ));
        setIsActive(!isActive) 
    }    
    var getClassActive = () => (isActive ? " active" : " ")
    var onAnimStart = (event, path) => {
        console.log(buttons)
        console.log(event, event.animationName, event.target.id, event.target.tagName)
        onRedirect(path)
    }
    var renderRadialButtons = () => {
        return buttons.map((rb, index) => { 
            return (
            <li id={index} className={rb.direction + " menu-item " + (rb.selected ? "selected" : "")} key={index} style={setN(index)} 
            onClick={() => setSelected(rb.path)} onAnimationStart={(event) => onAnimStart(event, rb.path)}>
                <a ><span className="icon">{rb.text}</span></a>
            </li>
        )})
    }
    var setSelected = (path) => { 
        setButtons(buttons.map(b => {
            b.selected = path === b.path; return b;
        }))
    } 

    useEffect(() => {
        return function clean(){ console.log('radial clean')}
    })

    return (
        <>
            <CakeSvg/>
            <ul className={"menu" + getClassActive()}>
                {renderRadialButtons()}
                <div className={"button" + getClassActive()} onClick={onNavClick}><MenuBar/></div>
            </ul>
        </>
    )
}

export default RadialNav