import React from 'react'
// import './home.scss'
import SpanText from '../../Components/SpanText';


const credentials = [
    {text : 'Design'}, 
    {text : 'Functionality'}, 
    {text :'Stonks'}
]
export default function Credentials(){
    var renderCredentials = () => {
        return credentials.map(cred => <div className="cred spanned-highlight " key={cred.text}>
            <SpanText text={cred.text}/></div>)
    }

    return <div className="credentials">
        {renderCredentials()}
    </div>
    
}