import React, {useState, useEffect} from 'react';
const NAME_SHORT = 'Dellon', NAME_FULL = 'Dellon Collins'
export default function Salutation(){
    const [name, setName] = useState(); 
    var nameOnResize = () => { if(name != getName()) { setName(getName())} }
    var getName = () => { return window.innerWidth > 600 ? NAME_FULL : NAME_SHORT; }
    window.addEventListener('resize', () => { nameOnResize() })
    useEffect(() => { nameOnResize() })
    
    return <div className="salutation">
        <div className="greeting">Hi, my name's</div>
        <div className="name">{name}</div>
        <div className="pronun">pronunciation : [duh-lawn]</div>
    </div>
}